@keyframes popUp {
  0%{top: 0px;};
  100%{top: 10px;}
}
*{
  margin: 0;
  box-sizing: border-box;
  font-family: 'Advent Pro', sans-serif;
}
body{
  margin: 0;
  background-color: rgb(67, 67, 192);
}
button{
  cursor: pointer;
}
#root{
  display: flex;
  justify-content: center;
}
.main_body{
  background-color: white;
  width: 500px;
  min-height: 100vh;
}
.home_main{
  padding: 10px 30px;
  width: 100%;
}
h1{
  text-align: center;
}
.add_div{
  border-radius: 5px;
  background-color: white;
  margin-top: 15px;
  box-shadow: 0 3px 8px #00000047;
}
.add_icon:focus{
  animation-duration: 500ms;
  animation-name: popUp;
}
.add_icon{
  background-color: transparent;
  font-weight: bold;
  float: right;
  font-size: 24px;
  border-radius: 11px;
  border: 0;
}
.searchbar{
  margin-top: 10px;
}
.searchbar input{
  width: 78%;
  font-size: 23px;
  border: none;
  box-shadow: 0 3px 8px #00000029;
}
#dropdown{
  float: right;
  font-size: 23px;
  border: none;
  box-shadow: 0 3px 8px #00000029;
  width: 20%;
}
#dropdown:focus{
  border: 0;
  outline: none;
}
.del_all{
  text-align: center;
}
.all_btn {
  margin-top: 10px;
  background-color: red;
  border: none;
  padding: 10px;
  border-radius: 25px;
}
.field_error{
  text-align: center;
  font-weight: bold;
  margin-top: 8px;
  margin-bottom: 0px;
  color: red;
}
.card{
  border-radius: 5px;
  padding: 10px;
  margin-top: 10px;
  box-shadow: 0 3px 8px #0000008c;
  overflow: auto;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.del-btn{
  margin-top: 5px;
  border: 0;
  box-shadow: 0 2px 13px #0000005c;
  font-size: 17px;
  font-weight: bold;
  color: red;
  float: right;
  border-radius: 5px;
  
  letter-spacing: 2px;
}
.edit-btn{
  margin-top: 5px;
  border: 0;
  box-shadow: 0 2px 13px #0000005c;
  font-size: 17px;
  font-weight: bold;
  color: green;
  float: left;
  border-radius: 5px;
  letter-spacing: 2px;
}
.info h3,p{
  letter-spacing: 2px;
}
.searchbar input:focus{
  outline: none;
}
@media screen and (max-width: 1000px ){
  .main_body{
    width: 100%;
  }
  .del_all{
    position: fixed;
    bottom: 33px;
    left: 33px;
  }
  .all_btn {
    background-color: red;
    border: none;
    padding: 10px;
    border-radius: 25px;
}
}
/* popup */
.add_popup{
  display: flex;
  justify-content: center;
  width: 100%;
}
.inner_popup{
  position: fixed;
  top: 10%;
  border-radius: 10px;
  width: 85%;
  padding: 12px;
  background-color: white;
  box-shadow: 0 3px 8px #0000008c;
 }
.add_popup input{
  width: 100%;
  margin: 10px 0 2px 0;
  border: none;
  border-bottom: 2px solid blue;
  border-radius: 10px;
  font-size: 20px;
}
.add_popup button{
  border: none;
  font-size: 20px;
}
.add_popup input:focus{
  outline: none;
}
@media screen and (min-width: 1000px ){
  .inner_popup{
    width: 44%;
  }
}
@media screen and (min-width: 1100px ){
  .inner_popup{
    width: 33%;
  }
}